import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import axios from 'axios';

interface ConnectProps {
  web3: [],
  account: string
}


const BIO: React.FC<ConnectProps> = ({ web3,account}) => {


  const [name, setname] = useState('')    
  const [age, setage] = useState('')    
  const [country, setcountry] = useState('')    
  const [hair, sethair] = useState('')    
  const [body, setboby] = useState('')    
  const [height, setheight] = useState('')    
  const [job, setjob] = useState('')    
  const [genText, setgenText] = useState('')    


 function updateNFT(){
        let valuename = document.getElementById("name").value;
        let valueage = document.getElementById("age").value;
        let valuecountry = document.getElementById("country").value;
        let valuehair = document.getElementById("hair").value;
        let valuebody = document.getElementById("body").value;
        let valueheight = document.getElementById("height").value;
        let valuejob = document.getElementById("job").value;

        setname(valuename)
        setage(valueage)
        setcountry(valuecountry)
        sethair(valuehair)
        setboby(valuebody)
        setheight(valueheight)
        setjob(valuejob)

 }

  function cleanGPT(){
        setgenText("")
  }

  async function callGPT(){

                const json = JSON.stringify({ 
                    name: name,
                    age: age,
                    country: country,
                    hair: hair,
                    body: body,
                    height: height,
                    job: job
                })

                const headers = {
                      'content-type': 'application/json',
                }

                console.log(json)

                await axios.post("https://api.dirtypanties.xyz/gpt/",json, headers
                   ).then(async (response) => {
                            if (response.data)
                                {
                                    console.log("CHECK OK",response.data)
                                    setgenText(response.data["response"])
                                }

                   });


 }



 return(
    <Wrapper>
    { !account &&

                 <TitleLabel>
                 "Unlock Your Metamask or click Connect"
                  </TitleLabel>

    }
    { web3 && account &&
        <>
              <InputDiv>
                 <TitleLabel>
                    Generative Bio
                 </TitleLabel>
             </InputDiv>
              <But>
                {!genText  &&
                 <Dropdowndiv>
                                <div>
                                    <MintInput id="name" onChange={() => updateNFT()} placeholder="Name" />  
                                </div>
                                <div>
                                    <MintInput id="country" onChange={() => updateNFT()} placeholder="Country" />  
                                </div>
                                <div>
                                    <MintInput id="age" onChange={() => updateNFT()} placeholder="Age" />  
                                </div>
                                <div>
                                    <MintInput id="hair" onChange={() => updateNFT()} placeholder="Hair" />  
                                </div>
                                <div>
                                    <MintInput id="body" onChange={() => updateNFT()} placeholder="Body" />  
                                </div>
                                <div>
                                    <MintInput id="height" onChange={() => updateNFT()} placeholder="Height" />  
                                </div>
                                <div>
                                    <MintInput id="job" onChange={() => updateNFT()} placeholder="Job" />  
                                </div>
                                <Apply>
                                    <StyledLinkText onClick={() => callGPT()}>Apply</StyledLinkText>  
                                </Apply>
                 </Dropdowndiv>
                 }

                {genText &&
                 <DropdowndivRetry>
                                <ApplyRetry>
                                    <StyledLinkText onClick={() => cleanGPT()}>Retry</StyledLinkText>  
                                </ApplyRetry>
                     <DescriptionText>
                        {genText}
                     </DescriptionText>
                 </DropdowndivRetry>
                }

              </But>
        </>
      }
  </Wrapper>
  );
}


const Apply = styled.div`
    margin-top: 25%;
`;

const ApplyRetry = styled.div`
    margin-top: 5%;
`;



const Dropdowndiv = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30%;
    @media (min-width: 768px) {
        width: 100%;
    }

`;

const DropdowndivRetry = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    @media (min-width: 768px) {
        width: 100%;
    }

`;



const Wrapper = styled.section`
    display: flex;
    align-items: center;
    flex-direction: column;
`;



const InputDiv = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
`;

const But = styled.div`
    display: flex;
    align-items: center;
    flex-direction: column;
    @media (min-width: 768px) {
        flex-direction: column;
      }
`;



const TitleLabel = styled.label`

  display: flex;
  font-size: 2vw;
  font-style: normal;
  font-weight: lighter;
  line-height: 1px;
  width: auto;
  align-items: center;
  color: white;
  background-color: transparent;
  letter-spacing: 1px;
  text-decoration: none;
  max-width: 100%;

  @media (min-width: 768px) {
    border: 0px solid #D29EE9;
    font-size:3vw;
    letter-spacing: 2px;
    padding: 20px 150px;
    width: auto;
  }

`



const StyledLinkText = styled.span`

  cursor: pointer;
  border-radius: 50px;
  font-size: 2vw;

  font-style: normal;
  font-weight: 400;

  width: 50%;
  text-align: center;
  color: white;
  background-color: #9E0040;
  text-decoration: none;

  padding: 3px 30px 3px;

  letter-spacing: 0px;
  font-style: normal;

  font-family: "AuroraMedium";
  &:hover {
    color: white;
    background-color: red;
  }
  &.active {
    color: red;
  }
  @media (min-width: 768px) {
    font-size: 1.5vw;
  }
`



const MintInput = styled.input`

  border-radius: 20px;
  background-color: white;
  font-size: 3vw;
  border: 1px solid #000000;
  font-style: normal;
  align-items: center;
  text-align: center;

  font-family: "AuroraMedium";
  letter-spacing: 0px;

  width: 100%;
  margin-top: 3%;
  margin-bottom: 3%;

  ::placeholder {
      color:  #6230BB;
  }

  @media only screen and (min-width: 768px) {
    font-size: 1.5vw;
  }
`

const DescriptionText = styled.div`
    display: flex;
    align-items: center;
    color: white;
    font-family: "AuroraMedium";
    font-size: 2vw;
    width: 90%;
    align-items: left;
    justify-content: left;
    text-align: center;
    margin-top: 50px;
`


export default BIO
